body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: Roboto, serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1, h2, h3, h4{
  margin: 0 0;
}
#logo{
    cursor: pointer;
}
.navigation__link{
    color: white;
    -webkit-transition: color 0.5s;
    transition: color 0.5s;
    padding: 0 0.5rem;
    cursor: pointer;
}

.navigation__link:hover,
.navigation__link:active{
    color: lightgrey;
}
#section_1{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    text-align: center;
    margin-top: -65px; 
}

#section_2{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    padding: 70px 15px;
    line-height:1.3rem;
  }
  .section2__photo1{
    max-width: 100%;

  }
  .section2__title{
    text-align: right;
    padding-right: 15px;
    margin-bottom: 25px;
  }
  section {
    max-width: 1200px;
    margin: 0 auto;
    display: -webkit-flex;
    display: flex;

  }
  
  .column {
    margin: 10px;
    -webkit-flex-grow: 2;
            flex-grow: 2;
    -webkit-flex-shrink: 2;
            flex-shrink: 2;
    -webkit-flex-basis: 0;
            flex-basis: 0;
    text-align: center;
  }

  p{
    text-align: justify;
  }
  .social{
    display: -webkit-inline-flex;
    display: inline-flex;
  }
#section_3{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    text-align: center;
}
#footer{
    background: lightgray;
    color: black;
    margin: 0 0;
    text-align: center;
    padding: 15px 15px;
    font-family: 'Courier New', Courier, monospace;
}
.footer__contact{
    padding-top: 15px;
    display: -webkit-inline-flex;
    display: inline-flex;
}
.footer__phone, .footer__email{
    padding: 0 20px;
    font-size: .8rem;
}


.App {
  text-align: center;
}

.fullHeight{
  min-height: 100vh;
}

.lineHeight{
  line-height: 2.2rem;
}
